.form {
  display: flex;
  flex-direction: column;

  &__input {
    width: 656px;
    height: 41px;
    margin-bottom: 48px;
    border: 0;
    border-bottom: 1px solid #e1e1e1;
    font-weight: bold;
    font-size: 14px;
    line-height: 22px;
    color: #152032;
    background-color: #fcfcfd;

    &:last-of-type {
      margin-bottom: 105px;
    }
  }

  .send-form__button {
    margin: 0;

    &:hover {
      background-color: lightgreen;
    }
  }
}

@media screen and (max-width: 1024px) {
  .form {
    display: flex;
    justify-content: center;
    align-items: center;

    .send-form__button {
      margin-bottom: 150px;
    }
  }
}
